import { Fragment, useState, useEffect } from 'react'
import UiLoader from '@components/ui-loader'
import classnames from 'classnames'
// ** Invoice List Sidebar

// import AddQuiz from '../add'

// ** Store & Actions
import { useDispatch, useSelector } from 'react-redux'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter, HelpCircle } from 'react-feather'
import DataTable from 'react-data-table-component'
import {
  Card,
  //Input,
  Row,
  Col,
  //Form,
  FormGroup,
  Label,
  //CustomInput,
  Spinner,
  Button,
  Popover,
  PopoverHeader,
  PopoverBody,
  CardHeader,
  CustomInput
} from 'reactstrap'
import AppCollapse from '@components/app-collapse'
// ** Styles
import { useForm, Controller } from 'react-hook-form'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import {
  // getContestsListCSV,
  getuserreportdata,
  getuserreportdownload,
  userfrom,
  userto,
  redeemfrom,
  redeemto,
  referralfrom,
  referralto,
  getdaterange,
  clearData
  //getfootballcontestdownload 
} from '../../store/actions'

const ContestsList = () => {
  // ** Store Vars
  const CustomHeader = () => {

    return (
      <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">

      </div>
    )
  }

  const defaultValues = {
    reactSelect: null
  }

  const store = useSelector((state) => state.basereport)
  const { user_report } = store
  console.log(user_report)
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const { handleSubmit, control } = useForm({ defaultValues })
  const [popoverOpen, setPopoverOpen] = useState(false)
  const [val, setVal] = useState(false)
  const [picker, setPicker] = useState(store.user_from)
  const [dateto, setDateto] = useState(store.user_to)
  const [dataPerPage, setDataPerPage] = useState('')
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getuserreportdata())
  }, [dispatch])

  console.log('pagination', store.cricket_contest_perpage)

  useEffect(() => {
    if (store.isloading) {
      <Spinner />
    } else {
      setListData(store.user_report)
    }
  }, [store.isloading])

  useEffect(() => {
    dispatch(getuserreportdata(currentPage, store.user_from, store.user_to, dataPerPage))
    dispatch(redeemfrom(""))
    dispatch(redeemto(""))
    dispatch(referralfrom(""))
    dispatch(referralto(""))
  }, [currentPage])

  useEffect(() => {
    setCurrentPage(1)
    dispatch(getuserreportdata(1, store.user_from, store.user_to, dataPerPage))
  }, [dataPerPage])

  const edittime = (date) => {
    setVal(false)
    const local = new Date(date)
    const datestring = `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setPicker(datestring)
  }

  const edit = (date) => {
    setVal(false)
    const local = new Date(date)
    const datestring = `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setDateto(datestring)
  }

  const dateConverter = (startDate, timeEnd) => {
    const newStartDate = new Date(startDate)
    const newEndDate = new Date(timeEnd)
    const one_day = 1000 * 60 * 60 * 24
    let result = ''
    result = Math.ceil((newEndDate.getTime() - newStartDate.getTime()) / (one_day))
    console.log('date Converter result', result)
    if (result < 0) { return 0 }
    dispatch(getdaterange(result))
    return result
  }

  const onSubmit = (e) => {
    e.preventDefault()
    setVal(true)
    if ((picker === "" || picker === 'NaN-NaN-NaN') || (dateto === "" || dateto === 'NaN-NaN-NaN')) {
      console.log(val)
    } else {
      setCurrentPage(1)
      dispatch(getuserreportdata(currentPage, picker, dateto))
      dispatch(userfrom(picker))
      dispatch(userto(dateto))
      dateConverter(picker, dateto)
    }
  }
  const download = () => {
    dispatch(getuserreportdownload(picker, dateto))
  }

  // ** User filter options

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setCurrentPage(page.selected + 1)
  }

  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.user_report.filter((userItem) => {

        if (userItem.contest_name) {
          return userItem.contest_name.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.user_report)
    }
  }, [searchTerm])

  useEffect(() => {
    return () => {
      dispatch(clearData())
    }
  }, [])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {

    const count = store.user_page.last_page
    //console.log(count)
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    // if (store.allContests.length > 0) {
    //   return store.allContests
    // } else
    if (listData.length === 0 && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric', hour: 'numeric', minute: 'numeric', second: 'numeric' }) => {
    if (!value) return value
    return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))

  }
  const columns = [
    {
      name: '#',
      minWidth: '100px',
      cell: (row, pos) => `#${store.user_page.from + pos}`
    },
    {
      name: 'User Name',
      minWidth: '160px',
      selector: 'team_name',
      sortable: true,
      cell: (row) => (row.team_name ? row.team_name.charAt(0).toUpperCase() + row.team_name.slice(1) : "-")
    },
    {
      name: 'Email',
      minWidth: '260px',
      selector: 'email',
      sortable: true,
      cell: (row) => row?.email
    },
    {
      name: 'Phone no. ',
      minWidth: '160px',
      selector: 'mobile',
      sortable: true,
      cell: (row) => row?.mobile
    },
    {
      name: 'Coin Balance',
      minWidth: '160px',
      selector: 'money.total_balance',
      sortable: true,
      cell: (row) => row?.money.total_balance
    },
    {
      name: 'DOB',
      minWidth: '160px',
      selector: 'user_account.dob',
      sortable: true,
      cell: (row) => row?.user_account.dob
    },
    {
      name: 'Gender',
      minWidth: '160px',
      selector: 'user_account.sex',
      sortable: true,
      cell: (row) => row?.user_account.sex
    },
    {
      name: 'Country',
      minWidth: '160px',
      selector: 'user_account.country',
      sortable: true,
      cell: (row) => row?.user_account.country
    },
    {
      name: 'State',
      minWidth: '160px',
      selector: 'user_account.state',
      sortable: true,
      cell: (row) => row?.user_account.state
    },
    {
      name: 'City',
      minWidth: '160px',
      selector: 'user_account.city',
      sortable: true,
      cell: (row) => row?.user_account.city
    },
    {
      name: 'Address 1',
      minWidth: '160px',
      selector: 'user_account.address1',
      sortable: true,
      cell: (row) => row?.user_account.address1
    },
    {
      name: 'Address 2',
      minWidth: '160px',
      selector: 'user_account.address2',
      sortable: true,
      cell: (row) => row?.user_account.address2
    },
    {
      name: 'Zipcode',
      minWidth: '160px',
      selector: 'user_account.zipcode',
      sortable: true,
      cell: (row) => row?.user_account.zipcode
    },
    {
      name: 'Member Since',
      minWidth: '240px',
      selector: 'fixture_name',
      sortable: true,
      cell: (row) => formatDate(row?.created_at)
    },
    {
      name: 'Last Login',
      minWidth: '160px',
      selector: 'fixture_name',
      sortable: true,
      cell: (row) => row?.last_login
    },

    {
      name: 'Referral Code',
      minWidth: '160px',
      selector: 'referral_code',
      sortable: true,
      cell: (row) => (row.referral_code ? row.referral_code : "-")
    },
    {
      name: 'Referal By',
      minWidth: '160px',
      selector: 'refer_from.referral.team_name',
      sortable: true,
      cell: (row) => (row.team_name ? row.team_name : "-")
    },
    {
      name: 'User',
      minWidth: '160px',
      selector: 'refer_by.referby.team_name',
      sortable: true,
      cell: (row) => (row.refer_by ? row.refer_by.referby ? row.refer_by.referby ? row.refer_by.referby.team_name : "" : "" : "")
    }
  ]

  const data = [
    {
      title: <div><Filter size={18} /> Filter</div>,
      content: (
        <>
          <Row className='mx-auto'>
            <Col className="d-flex align-items-center p-0">
              <div className="d-flex align-items-center w-100">

                <Label className="mb-0">From:</Label>&nbsp;&nbsp;
                {/* <Flatpickr
                value={store.user_from}
                id='range-picker'
                className='form-control'
                //  value={picker}
                //  onChange={date => setPicker(date)}
                onChange={date => edittime(date)}      
               /> */}
                <Controller
                  // as={Select}
                  control={control}
                  render={({ field: { ...field } }) => (
                    <Flatpickr
                      {...field}
                      value={store.user_from}
                      placeholder='select from date'
                      onChange={date => edittime(date)}
                      options={{
                        // defaultDate : '01-05-1999',
                        dateFormat: "Y-m-d"
                      }}
                      id='range-picker'
                      // theme={selectThemeColors}
                      className={classnames('form-control', { 'is-invalid': (picker === "" && val) || (picker === 'NaN-NaN-NaN' && val) })}
                    // classNamePrefix='select'
                    // isClearable={true}
                    />
                  )}
                  name='reactSelect'
                // defaultValue={series[0]}
                />
                &emsp;&nbsp;

                <Label className="mb-0">To:</Label>&nbsp;&nbsp;
                {/* <Flatpickr
                  value={store.user_to}
                  id='range-picker'
                  className='form-control'
                  onChange={dat => edit(dat)}
               />  */}

                <Controller
                  // as={Select}
                  control={control}
                  render={({ field: { ...field } }) => (
                    <Flatpickr
                      {...field}
                      value={store.user_to}
                      placeholder='select to date'
                      onChange={dat => edit(dat)}
                      options={{
                        // defaultDate : '01-05-1999',
                        dateFormat: "Y-m-d"
                      }}
                      id='range-picker'
                      // theme={selectThemeColors}
                      className={classnames('form-control', { 'is-invalid': (dateto === "" && val) || (dateto === 'NaN-NaN-NaN' && val) })}
                    // classNamePrefix='select'
                    // isClearable={true}
                    />
                  )}
                  name='reactSelect'
                // defaultValue={series[0]}
                />
                &emsp;&nbsp;
              </div>
            </Col>
          </Row>
          {(picker === "" && val) || (picker === 'NaN-NaN-NaN' && val) || (dateto === "" && val) || (dateto === 'NaN-NaN-NaN' && val) ? <h5 className='text-danger ml-3'>Please select a date</h5> : ""}
          <Row className='ml-1 mt-1'>
            <FormGroup className="d-flex mb-0">
              <Button.Ripple
                className="mx-2"
                color="primary"
                type="submit"
                onClick={(e) => handleSubmit(onSubmit(e))}
              >
                Submit
              </Button.Ripple>
            </FormGroup>
          </Row>
        </>
      )
    }
  ]

  return (

    <Fragment>
      <AppCollapse data={data} type='border' accordion className='mb-1' />
      <Card>
      <CardHeader>
        <Row xl={12} className='d-flex w-100'>
        <Col xl="4">
              <Label for="rows-per-page">Show</Label>
              <CustomInput
                className="form-control mx-50"
                type="select"
                id="rows-per-page"
                value={dataPerPage}
                onChange={(e) => setDataPerPage(e.target.value)}
                style={{
                  width: '5rem',
                  padding: '0 0.8rem',
                  backgroundPosition:
                    'calc(100% - 3px) 11px, calc(100% - 20px) 13px, 100% 0'
                }}
              >
                <option value="10">10</option>
                <option value="50">50</option>
                <option value="100">100</option>
              </CustomInput>
              <Label for="rows-per-page">Entries</Label>
          </Col>
          <Col className='ml-auto' xl={{offSet:5}}>
                <Button.Ripple 
                className='ml-2'
                color='primary'
                disabled={store.date_range > 90}
                onClick ={() => download()}>      
                {store.download_loading ?  
                <>
                <Spinner color='white' size='sm' />
                <span className='ml-50'>Loading...</span>
                </> : 'Download CSV' }
      </Button.Ripple>
      <Button.Ripple
                outline id='controlledPopover'
                color='flat-info'
                >
                  <HelpCircle />
                </Button.Ripple>
      <Popover
        placement='top'
        target='controlledPopover'
        isOpen={popoverOpen}
        toggle={() => setPopoverOpen(!popoverOpen)}
      >
        <PopoverHeader>INFO</PopoverHeader>
        <PopoverBody>
                  pick a date less than 90 days to download
        </PopoverBody>
      </Popover>
          </Col>
        </Row>
        </CardHeader>
        <DataTable
          noHeader
          // pagination
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              // handlePerPage={handlePerPage}
              rowsPerPage
              searchTerm={searchTerm}
              handleFilter={handleFilter}
              currentPage={currentPage}
              setCurrentPage={setCurrentPage}
            />
          }
        />
      </Card>
      {
        listData.length === 0 ? '' : <CustomPagination />
      }
    </Fragment>

  )
}

export default ContestsList
